.sky {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  overflow: hidden;
}

.star {
  position: absolute;
  width: 3px;
  height: 3px;
  background: white;
  border-radius: 50%;
  box-shadow: 0 0 10px rgba(255, 255, 255, 0.8);
  animation: fall linear infinite;
  top: -10px;
}

@keyframes fall {
  0% {
    transform: translateY(-10px);
    opacity: 1;
  }
  100% {
    transform: translateY(100vh);
    opacity: 0;
  }
}

.container {
  position: relative;
  z-index: 1;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  width: 100vw;
  color: white;
  padding: 40px 20px;
}

.typing-container {
  text-align: center;
  margin-bottom: 50px;
  animation: fadeIn 1s ease-in;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(-20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.typing-text {
  font-size: 5rem;
  font-weight: bold;
  margin-bottom: 20px;
  background: linear-gradient(45deg, #fff, #f0f0f0);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  text-shadow: 0 0 30px rgba(255, 255, 255, 0.5);
  animation: glow 2s ease-in-out infinite alternate;
}

@keyframes glow {
  from {
    filter: drop-shadow(0 0 20px rgba(255, 255, 255, 0.3));
  }
  to {
    filter: drop-shadow(0 0 30px rgba(255, 255, 255, 0.6));
  }
}

.bio-text {
  font-size: 1.8rem;
  margin-bottom: 30px;
  opacity: 0.95;
  animation: fadeIn 1.5s ease-in 0.5s both;
}

.social-icons {
  display: flex;
  gap: 30px;
  justify-content: center;
  margin-bottom: 40px;
  animation: fadeIn 2s ease-in 1s both;
}

.social-icons a {
  color: white;
  transition: all 0.3s ease;
  filter: drop-shadow(0 0 10px rgba(255, 255, 255, 0.3));
}

.social-icons a:hover {
  transform: scale(1.3) rotate(5deg);
  filter: drop-shadow(0 0 20px rgba(255, 255, 255, 0.8));
}

.button-group {
  display: flex;
  gap: 20px;
  justify-content: center;
  animation: fadeIn 2.5s ease-in 1.5s both;
}

.custom-button {
  padding: 15px 35px;
  font-size: 1.2rem;
  border: 3px solid white;
  border-radius: 30px;
  background: rgba(255, 255, 255, 0.1);
  color: white;
  text-decoration: none;
  transition: all 0.4s ease;
  cursor: pointer;
  backdrop-filter: blur(10px);
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.2);
}

.custom-button:hover {
  background: white;
  color: #667eea;
  transform: translateY(-5px);
  box-shadow: 0 15px 40px rgba(255, 255, 255, 0.4);
}

footer {
  text-align: center;
  padding: 20px;
  color: rgba(255, 255, 255, 0.7);
  font-size: 0.9rem;
  margin-top: 50px;
}

.content-section {
  position: relative;
  z-index: 1;
  max-width: 1100px;
  margin: 0 auto;
  padding: 120px 24px;
}

.content-section h2 {
  font-size: 2.4rem;
  margin-bottom: 16px;
}

.content-section p {
  font-size: 1.1rem;
  opacity: 0.95;
}

.section.last {
  padding-bottom: 160px;
}

@media (max-width: 900px) {
  .container {
    flex-direction: column;
    gap: 28px;
  }
  .typing-text {
    font-size: 3rem;
  }
  .bio-text {
    font-size: 1.2rem;
  }
  .button-group {
    flex-direction: column;
  }
}

